import clsx from 'clsx';
import React, { forwardRef } from 'react';
import {
    TextBubbleDefaultProps,
    TextBubblePropTypes
} from './generated/propTypes';

const TextBubble = forwardRef((props, externalRef) => {
    const {
        tag: CompTag,
        text,
        bubbleSize,
        themeColor,
        children,
        variant,
        ...customProps
    } = props;

    return (
        <CompTag
            ref={externalRef}
            className={clsx(
                'lmnt lmnt-text-bubble',
                `lmnt-text-bubble__theme-color--${themeColor}`,
                variant === 'outlined' && 'lmnt-text-bubble--outlined',
                bubbleSize === 'small' && 'lmnt-text-bubble--small'
            )}
            {...customProps}
        >
            {text}
            {children}
        </CompTag>
    );
});

TextBubble.displayName = 'TextBubble';
TextBubble.propTypes = TextBubblePropTypes;
TextBubble.defaultProps = TextBubbleDefaultProps;
export default TextBubble;
